import React,{useState} from 'react'
import banner_1 from '../asset/banner_1.png'
import Carousel from 'react-bootstrap/Carousel'
import './Banner.css'


const Banner = () => {
    const [crrItem, setCrrItem] = useState(0)

    
   
  return (
<>
<div className='container-fluid' style={{padding:0}}>
<Carousel variant="dark" prevIcon={""}  nextIcon={""} >
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={banner_1}
          alt="First slide"
        />
       
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={banner_1}
          alt="Second slide"
        />
       
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={banner_1}
          alt="Third slide"
        />
        
       
      </Carousel.Item>
    </Carousel>

</div>



</>

  )
}

export default Banner