import React from 'react'
import "./navbar.css"
import logo from '../asset/image 1.png'


const Navigationbar = () => {
  return (
   
    <div className='container-fluid main-div '>
        <div className='row between-div'>
            <div className='col-md-3 center-div'>
            <img src={logo} alt=""></img>
            </div>

            <div className='col-md-5 menu'>
           
           <ul>
                 <li>Home</li>
                 <li>About</li>
                 <li>Contact</li>
                 <li>Product</li>
             </ul>
        
       
            </div>
            
            <div className='col-md-4 center-div menu '>
            
           <ul>
                    <li><i className="bi bi-search"></i></li>
                 <li><i className="bi bi-cart"></i></li>
                 <li><i className="bi bi-heart"></i></li>
                 <li><button className='btn-design'>Sign in</button></li>
             </ul>
      
       
            </div>
        </div>





    </div>

   
   

   
  )
}

export default Navigationbar


